@charset "UTF-8";
*,
*::after,
*::before {
  background-repeat: no-repeat;
  box-sizing: inherit;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  max-width: 1440px;
  color: var(--c-3);
  overflow-x: hidden;
  margin: 0 auto;
}

:root {
  --c-1: rgb(222, 179, 126);
  --c-2: rgb(84, 82, 79);
  --c-3: rgb(255, 255, 255);
  --c-4: hsl(33, 71%, 61%);
}

:root {
  --f-mon: "Montserrat", sans-serif;
  --f-open: "Open Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  transform-origin: left bottom;
}

span {
  display: block;
  transform-origin: left bottom;
}

/* Chỉ cho phép textarea được kéo dãn theo chiều dọc, trông sẽ đẹp hơn, không làm xấu layout */
textarea {
  resize: vertical;
}

/* Xóa bỏ khoảng trắng thừa dưới image */
img {
  vertical-align: middle;
}

/* Cũng để dễ control theo design, nên mình thường reset luôn ul, ol */
ul,
ol {
  list-style: none;
}

/* Tạo hiệu ứng bôi đen text khác biệt hơn mặc định */
::selection {
  background: #0031a2;
  color: #fff;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  color: inherit;
}

.container {
  max-width: 78.875rem;
  margin: 0 auto;
}
.container--tb-lg {
  padding-top: 23.25rem;
  padding-bottom: 13.875rem;
  padding-left: 0;
  padding-right: 0;
}
.container--tb-md {
  padding-top: 2.8125rem;
  padding-bottom: 11.5rem;
  padding-left: 0;
  padding-right: 0;
}
.container--tb-lg {
  padding-top: 23.3125rem;
  padding-bottom: 13.75rem;
  padding-left: 0;
  padding-right: 0;
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none !important;
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none !important;
  }
}

.header {
  position: fixed;
  position: fixed;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
}
.header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__right {
  display: flex;
  align-items: center;
}
.header__nav {
  font-size: 1.0416875rem;
  color: #54524f;
  text-transform: uppercase;
  line-height: 1.2;
  font-weight: 700;
}
.header__nav a {
  margin-right: 3.75rem;
}
.header__search {
  position: relative;
}
.header__search input {
  padding: 0.9375rem;
  background-color: #7f7770;
  opacity: 0.471;
  border-radius: 9999px;
  outline: none;
  border: none;
}
.header__search i {
  position: absolute;
  top: 50%;
  font-size: 1.875rem;
  right: 1.25rem;
  transform: translateY(-50%);
  color: #54524f;
  z-index: 9;
  pointer-events: none;
}

.hero {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.hero__content {
  max-width: 35%;
}
.hero__content h1 {
  color: #deb37e;
  font-weight: 800;
  font-size: 3.7051875rem;
  text-shadow: 0px 5px 5px rgba(84, 82, 79, 0.22);
  line-height: 1.2;
  transform: matrix(1.546293187, 0.0073901121, -0.0073898277, 1.5462336645, 0, 0);
  padding-bottom: 2.0625rem;
}
.hero__content span {
  display: block;
  color: #54524f;
  font-size: 1.4375rem;
  font-weight: 700;
  font-style: italic;
  transform: matrix(1.0851137218, 0, 0, 1.0851137218, 0, 0);
  line-height: 1.2;
  padding-bottom: 4.1875rem;
}
.hero__content .btn {
  margin-bottom: 1.5625rem;
}
.hero__content p {
  color: #54524f;
  font-style: italic;
  transform: matrix(1.0851137218, 0, 0, 1.0851137218, 0, 0);
  font-size: 1.1875rem;
  line-height: 1.2;
}

.btn {
  display: inline-block;
  font-size: 1.5625rem;
  line-height: 1.2;
  transform: matrix(0.9989040473, 0, 0, 0.9992384078, 0, 0);
  transform-origin: left bottom;
  padding: 0.9375rem 2.375rem;
  background-color: #776c64;
  border-radius: 9999px;
  font-weight: 800;
  text-transform: uppercase;
}
.btn--bg {
  background-color: #e2a355;
}

.go {
  text-align: center;
}
.go__title {
  text-align: center;
  max-width: 48.75rem;
  margin: 0 auto;
  margin-bottom: 5.125rem;
}
.go__title h2 {
  color: #e2a355;
  text-transform: uppercase;
  font-size: 5.125rem;
  transform: matrix(0.7614451893, 0, 0, 0.761370364, 0, 0);
  font-weight: 800;
  line-height: 1.2;
  transform-origin: top;
}
.go__title p {
  font-size: 1.875rem;
  color: #7f7770;
  transform-origin: top;
  line-height: 1.2;
}
.go__grid {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-bottom: 3.5rem;
}
.go__item {
  flex-basis: 21.25rem;
}
.go__item img {
  object-fit: cover;
  object-position: center;
}
.go__content {
  background-color: #7f7770;
  padding: 0 0.9375rem;
  text-align: left;
}
.go__content span {
  transform: matrix(0.9982558668, 0, 0, 1, 0, 0);
  font-weight: 700;
  line-height: 1.2;
  font-size: 1.8125rem;
  padding-top: 3.625rem;
  padding-bottom: 1.875rem;
}
.go__content p {
  font-size: 1.4375rem;
  line-height: 1.2;
  transform: matrix(0.9982558668, 0, 0, 1, 0, 0);
  padding-bottom: 4.5rem;
}

.price__grid {
  display: flex;
  flex-direction: column;
}
.price__item {
  background-color: #7f7770;
  display: flex;
  align-items: center;
}
.price__item:nth-child(2) {
  background-color: #deb37e;
}
.price__item:nth-child(2) .price__content {
  order: -1;
}
.price__item img {
  flex: 1;
  object-fit: cover;
  object-position: center;
}
.price .wrapper {
  max-width: 32.25rem;
}
.price__content {
  flex: 1;
  padding-left: 3.5rem;
  text-align: left;
}
.price__content span {
  font-size: 3.6458125rem;
  line-height: 1.2;
  padding-bottom: 3.375rem;
  font-weight: 700;
}
.price__content p {
  font-size: 1.6875rem;
  padding-bottom: 3.75rem;
}

.hiking {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.hiking__title {
  text-align: center;
}
.hiking__title h2 {
  font-size: 3.6421875rem;
  transform: matrix(1.0005254861, 0, 0, 1.0009932863, 0, 0);
  line-height: 1.2;
  font-weight: 600;
  padding-bottom: 2.75rem;
}
.hiking__title p {
  font-size: 1.8125rem;
  line-height: 1.2;
  transform: matrix(1.0005254861, 0, 0, 1.0009932863, 0, 0);
}

.explore {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.explore .container {
  padding-top: 30.4375rem;
  padding-bottom: 27.625rem;
}
.explore .wrapper {
  max-width: 31.25rem;
  margin-left: auto;
}
.explore span {
  font-size: 4.8934375rem;
  font-style: italic;
  font-weight: 800;
  text-align: right;
  color: #deb37e;
  text-shadow: 0px 5px 5px rgba(84, 82, 79, 0.36);
  line-height: 1.2;
  transform: matrix(0.9880385904, 0.0045782464, -0.0047218956, 0.9579067935, 0, 0);
}
.explore span span {
  color: #54524f;
}

.title-here {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.title-here .container {
  padding-top: 14.375rem;
  padding-bottom: 14.375rem;
}
.title-here .wrapper {
  max-width: 56.25rem;
  margin: 0 auto;
  text-align: center;
}
.title-here .wrap-input {
  position: relative;
  max-width: 50rem;
  margin: 0 auto;
}
.title-here span {
  font-size: 4.6313572rem;
  font-weight: 800;
  line-height: 1.2;
  padding-bottom: 4.625rem;
}
.title-here p {
  font-size: 1.4375rem;
  padding-bottom: 4rem;
}
.title-here input {
  width: 100%;
  padding: 1.375rem 2.25rem;
  border-radius: 9999px;
  outline: none;
  border: none;
}
.title-here .btn {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}